import React from 'react';
import './Navbar.css';
import BNavbar from 'react-bootstrap/Navbar';
import BNav from 'react-bootstrap/Nav';
import BNavDropdown from 'react-bootstrap/NavDropdown';
import {Link} from 'react-router-dom';

const Navbar = (props) => {
    return (
        <BNavbar bg="light" expand="lg" className="navbar-holder">
            <BNavbar.Brand><Link to={'/contract-drafting-tool'} className="navbar-link">Contract Drafter</Link></BNavbar.Brand>
            <BNavbar.Toggle aria-controls="basic-navbar-nav" />
            <BNavbar.Toggle />
                {props.user ? 
                    <BNav>
                        <Link to={{
                            pathname:`/contract-drafting-tool/user/${props.user.uid}/create`,
                            state: { author: props.user.displayName } }} 
                            className="navbar-link navbar-link-item">Create Template</Link> 
                        <Link to={{
                            pathname: `/contract-drafting-tool/user/${props.user.uid}`,
                            state:{ author: props.user.displayName } }} 
                            className="navbar-link navbar-link-item">User Home</Link>
                    </BNav>:<BNav></BNav>
                }
            <BNavbar.Collapse className="justify-content-end">
                <BNav >
                    <BNavbar.Brand href="/user">
                        <img
                            src={props.photo}
                            width="30"
                            height="30"
                            className="d-inline-block align-top"
                            alt="React Bootstrap logo"
                        />
                    </BNavbar.Brand>
                    {
                        props.signIn ?
                        <BNavDropdown title={props.name} id="basic-nav-dropdown">
                            {/* <BNavDropdown.Divider /> */}
                            <BNavDropdown.Item onClick={() => props.signIn()}>
                                Log In
                            </BNavDropdown.Item>
                        </BNavDropdown>
                        :
                        <BNavDropdown title={props.name} id="basic-nav-dropdown">
                            {/* <BNavDropdown.Divider /> */}
                            <BNavDropdown.Item onClick={() => props.auth.signOut()}>
                                Log Out
                            </BNavDropdown.Item>
                        </BNavDropdown>
                    }
                
                </BNav>
                
            </BNavbar.Collapse>
        </BNavbar>
    )
}

export default Navbar
